  background: transparent;
  color: var(--color-pink);
}

/* Feedback Form*/

.feedback-us__form {
  display: flex;
  flex-direction: column;
  margin-top: 25px;
}

.feedback-us__fields ~ .feedback-us__fields {
  margin-top: 22px;
}

.feedback-us__fields--col .feedback-us__fields ~ .feedback-us__fields {
  margin-top: 0;
}

.feedback-us__fields--col {
  display: flex;
  gap: 18px;
}

.feedback-us__field-title {
  margin-bottom: 8px;
}

.feedback-us__fields,
.feedback-us__input {
  width: 100%;
  border-radius: 12px;
}

.feedback-us__input {
  border: 1px solid rgb(230, 230, 230);
  outline: none;
  transition: border 0.3s ease-in-out;
  padding: 14px 21px;
}

.feedback-us__input--textarea {
  resize: none;
}

.feedback-us__file {
  position: relative;
}

.feedback-us__input--file {
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
  width: 0;
  height: 0;
}

.feedback-us__file .feedback-us__input--file[type=file]:focus + .feedback-us__file-btn {
  text-decoration: underline;
}

.feedback-us__file-btn {
  position: relative;
  display: block;
  padding-left: 30px;
  text-decoration: underline;
}

.feedback-us__file-btn:before {
  content: url("data:image/svg+xml;base64,");
  position: absolute;
  top: 2px;
  left: 5px;
}

.feedback-us__input::-moz-placeholder {
  color: rgb(179, 179, 179);
}

.feedback-us__input::placeholder {
  color: rgb(179, 179, 179);
}

.feedback-us__input:focus,
.feedback-us__input:target,
.feedback-us__input:hover {
  border: 1px solid var(--color-gray);
}

.feedback-us__checkbox {
  margin-right: 8px;
  position: relative;
  z-index: -1;
  opacity: 0;
}

.feedback-us__checkbox-fake {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 5px;
}

.feedback-us__agree-text:before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  z-index: 1;
  opacity: 1;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 10px;
  border: 1px solid rgb(179, 179, 179);
  width: 18px;
  height: 18px;
}

.feedback-us__checkbox-fake > input:checked + div::before {
  background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTAiIGhlaWdodD0iMTAiIHZpZXdCb3g9IjAgMCAxMCAxMCIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTEwIDBIMFYxMEgxMFYwWiIgZmlsbD0iI0U5NDU0RiIvPgo8L3N2Zz4K");
}

.feedback-us__link-agree {
  color: var(--color-black);
  text-decoration: underline;
}

.feedback-us__fields--col-footer {
  justify-content: space-between;
}

.button {
  font-family: var(--font-main);
  font-weight: 500;
  font-size: 16px;
  text-align: center;
  border-radius: 11px;
  padding: 15px 40px 15px;
  outline: none;
  border: none;
  text-transform: uppercase;
  cursor: pointer;
}

.button--primary {
  color: var(--color-white);
  background: var(--color-pink);
}

/* MEDIA QUERIES */

@media screen and (max-width: 1365px) {
  .contacts__inner {
    grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  }
}

@media screen and (max-width: 1024px) {
  .hide-tablet {
    display: none !important;
  }

  .title--indent {
    margin: 0 0 36px;
  }

  .contacts__inner {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 14px;
  }
}

@media screen and (max-width: 992px) {
  .hide-tabletSM {
    display: none !important;
  }

  .title-divider__text {
    margin: 0 20px;
  }

  .slider-nav {
    display: none;
  }

  .slider-blog {
    padding: 30px 0 30px;
  }

  .post__body {
    padding: 30px 26px 30px;
  }

  .contacts__map {
    display: none;
  }

  .contacts__mobile-map {
    display: block;
  }

  .contacts__plunk-map {
    position: static;
    transform: translateY(0);
    padding: 22px 50px 34px 22px;
    box-shadow: 0 4px 17px 0 rgba(157, 157, 157, 0.25);
  }

  .contacts__mobile-br {
    display: block;
  }

  .contacts__item {
    padding: 22px 22px 33px 22px;
  }
}

@media screen and (max-width: 768px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .container-full {
    padding-left: 15px;
    padding-right: 15px;
  }

  .hide-mobile {
    display: none !important;
  }

  .feedback-us__fields--col {
    flex-direction: column;
  }
}

@media screen and (max-width: 576px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }

  .container-full {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media (hover: hover) {
  .f-button:hover:not([disabled]) {
    color: var(--f-button-hover-color);
    background-color: var(--f-button-hover-bg);
  }
}